<template>
    <div id="sendDiv">
        <v-textarea 
          name="input-7-1"
          label="Comando"
          v-model.lazy="textA"
          hint="Inserisci il comando"
            id="ta_send" placeholder="Comando"></v-textarea>
        <v-btn depressed elevation="2" raised @click="sendmsg"> Send data </v-btn>

    </div>
</template>
<script>
  import {eventBus} from '../main.js';
export default {
    name : 'barSend',
    data(){
        return {
            textA : ''
        }
    },
    methods : {
        sendmsg(){
            eventBus.sendMessage('##BEGIN-PROCESS##\n'+this.textA+'\n##END-PROCESS##')
        }
    },
    props : {
        colore : {
            type : String,
            default : 'Celeste'
        },

    }
}
</script>
