<template>
    <div id="recDiv">
        <v-container>
            <v-row>
            <v-col cols="9">
                <v-textarea 
                    name="input-7-1"
                    rows="10"
                    row-height="7"
                    label="Response"
                    v-model.lazy="textR"
                    hint="Risposta"
                    id="ta_rec" placeholder="Response" readonly>
                </v-textarea>
            </v-col><v-col>
                <config-c></config-c>
            </v-col>
            </v-row>
        </v-container>
    </div>
</template>
<script>
  import {eventBus} from '../main.js';
  import ConfigC from './configComp.vue'
    export default {
    name : 'barRec',
    components : {
      ConfigC
    },
    data(){
        return {
            textR : ''
        }
    },
     created (){
         eventBus.$on('cambioText',(v)=>{this.textR += v;});
         this.$options.sockets.onmessage = (message) => {if(!message.data.includes('##BEGIN-SERVER-CONF##')){ this.textR+='\n'+message.data }};
         //vm.$on('receivedData',(v)=>{this.textR+=v;})
    }
}
</script>