<template>
    <div id="ConfigDiv">
        <v-textarea 
            filled
            name="input-7-4"
            rows="10"
            row-height="2"
            label="Config"
            v-model.lazy="textConf"
            hint="Configurazione"
            id="ta_conf" placeholder="Configurazione" readonly>
        </v-textarea>
    </div>
</template>
<script>
    export default {
    name : 'configDiv',
    data(){
        return {
            textConf : ''
        }
    },
     created (){
         this.$options.sockets.onmessage = (message) => {if(message.data.includes('##BEGIN-SERVER-CONF##')){ this.textConf= message.data}};
         //vm.$on('receivedData',(v)=>{this.textR+=v;})
    }
}
</script>